import React from 'react';
import './OrderPopup.css'; //    CSS   Popup

const OrderPopup = ({ onClose, orderDetails, webAppQueryId }) => {
    return (
        <div className="popup-overlay">
            <div className="popup-content">
                <h2>   !</h2>
                <p>ID  : {webAppQueryId}</p>
                {orderDetails ? (
                    <div className="order-details">
                        <h3>:</h3>
                        <ul>
                            {orderDetails.items.map((item, index) => (
                                <li key={index}>
                                    <strong>{item.title}</strong> - {item.quantity} . - {item.total} VND
                                    {item.toppings && item.toppings.length > 0 && (
                                        <div>: {item.toppings.join(', ')}</div>
                                    )}
                                </li>
                            ))}
                        </ul>
                        <h3>: {orderDetails.totalPrice} VND</h3>
                    </div>
                ) : (
                    <p>  .</p>
                )}
                <button className="popup-close-button" onClick={onClose}>
                        
                </button>
            </div>
        </div>
    );
};

export default OrderPopup;
